            return{
                contentlist:[
                    {
                        img:"static/a2.jpg",
                        author:"读芯术",
                        read:0,
                        tittle:"python 界面一",
                        label:[
                            'python',
                        ],
                        time:"2019-09-01",
                        intros:"EasyGui 安装" ,
                        href:"http://114.115.128.109:8089/#/view/29"
                    },
                    {
                        img:"static/a1.jpeg",
                        author:"读芯术",
                        read:0,
                        tittle:"内存管理总结",
                        label:[
                            'java',
                        ],
                        time:"2019-09-01",
                        intros:"局部变量也叫auto自动变量(auto可写可不写)，一般情况下代码块{}内部定义的变量都是自动变量",
