    color:#969696;
    margin:1% 0 1% 0;
  }

  .article_tittle{
    font-weight:  bold;
    font-size: 18px;
    color: black;
    vertical-align: middle;
    margin:0 0 1% 0;
  }

  .article_tittle:hover{
    color:#e69800;
  }

  .platetittle{
    background-color: #fff;
    height:200px;
    border-bottom: 1px solid #f1f1f1;
    margin-left:15%;
    margin-right:5%;
    display:flex;
  }

  .plateimg{
    margin :auto 0;
    border-radius: 5%;
  }

  .plate_welcome{
    font-weight: bold;
    font-size: 45px;
    margin :auto 0 auto 5%;
  }




</style>



